.image {
	position: relative;
	width: 400px;

    &__img {
        display: block;
        width: 100%;
    }
    
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
        color: #ffffff;
        font-family: 'Quicksand', sans-serif;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transition: opacity 0.25s;
    }
    
    &__overlay--blur {
        backdrop-filter: blur(5px);
    }
    
    &__overlay--primary {
        background: #009578;
    }
    
    &__overlay>* {
        transform: translateY(20px);
        transition: transform 0.25s;
    }
    
    &__overlay:hover {
        opacity: 1;
    }
    
    &__overlay:hover>* {
        transform: translateY(0);
    }
    
    &__title {
        font-size: 2em;
        font-weight: bold;
    }
    
    &__description {
        font-size: 1.25em;
        margin-top: 0.25em;
    }
}

