@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    display: flex;
    flex-direction: column;

    &__campo {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }

    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$negro, .8);
    }

    &__input,
    &__select {
        padding: 1rem;
        border: 1px solid darken(v.$gris-claro, 20);
        border-radius: .5rem;
    }

    &__texto {
        margin-top: 3rem;
        color: rgba(v.$negro, .8);
    }

    &__submit {
        @include m.boton(v.$azul, v.$blanco);    
    }
}