@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$azul;

    &__contenedor {
        @include m.contenedor;
        padding: 4rem 0;
    }

    &__grid {
        @include m.grid(1, 3rem);

        @include m.tablet {
            @include m.grid(2, 3rem);
        }

        @include m.desktop {
            @include m.grid(4, 3rem);
        }
    }

    &__heading {
        color: v.$blanco;
        font-weight: v.$bold;
        text-align: left;
    }

    &__texto {
        color: v.$secundario;
        margin: 1rem 0;
    }

    &__copyright {
        background-color: darken(v.$azul, 9);
        padding: .2rem 0;
    }

    &__copyright-texto {
        color: v.$blanco;
        text-align: center;
        font-size: 1.4rem;
    }
}