@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    //min-height: 60rem;
    height: 100vh;
    background-image: linear-gradient(to right, rgba(v.$negro, 1), rgba(v.$negro, .5)), image-set(
        url(../img/hero.avif) 1x,
        url(../img/hero.webp) 1x,
        url(../img/hero.jpg) 1x
    );
    background-repeat: no-repeat;
    background-image: cover;
    background-size: cover;
    display: flex;
    align-items: center;

    &__contenedor {
        @include m.contenedor;
    }

    &__logo {
        @include m.texto-fluido(2rem, 8rem);
        @include m.texto-degradado(v.$azul, v.$secundario);
        text-align: center;
        text-transform: unset;
        @include m.tablet {
            text-align: left;
            display: inline-block;
        }
    }

    &__texto {
        font-size: 3rem;
        font-weight: v.$bold;
        color: v.$blanco;
    }
    
}