@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    margin: 1rem 0 2rem 0;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin headings {
    font-family: v.$fuente-principal;
    font-weight: v.$black;
    margin: 2rem 0;
    text-align: center;
    text-transform: uppercase;
}

@mixin nombre-pagina {
    @include headings;
    margin: 6rem 0 3rem 0;
    color: v.$azul;
}

@mixin descripcion-pagina {
    text-align: center;
    font-size: 1.8rem;
    font-weight: v.$bold;
    color: v.$gris-oscuro;
    margin-bottom: 5rem;
    color: v.$secundario;
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
    width: min(95%, 140rem);
    margin: 0 auto;
}

@mixin contenedor-md {
    width: min(95%, 80rem);
    margin: 0 auto;
}

@mixin contenedor-sm {
    width: min(95%, 60rem);
    margin: 0 auto;
}

@mixin texto-fluido($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw ,$max);
}

@mixin texto-degradado($inicio, $final) {
    background: linear-gradient(to right, $inicio, $final);
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

@mixin logo {
    color: v.$blanco;
    text-align: center;
    margin-bottom: 2rem;
    //white-space: nowrap;
    @include desktop {
        text-align: left;
        margin: 0;
    }
}

@mixin enlace {
    all: unset;
    color: v.$blanco;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;
    cursor: pointer;

    &:hover {
        color: v.$azul;
    }
}

@mixin shadow {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
