@use 'base/variables' as v;
@use 'base/mixins' as m;

.proyectos {
    margin: 6rem 0;

    &__grid {
        @include m.contenedor;
        @include m.grid(1, 5rem);

        @include m.tablet {
            @include m.grid(2, 5rem);
        }

        @include m.desktop {
            grid-template-columns: unset;
            @include m.grid(3, 5rem);
        }
    }

    &__enlace-contenedor {
        margin-top: 5rem;
        display: flex;
        justify-content: center;
    }

    &__enlace {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
    }

    &__contenedor {
        @include m.contenedor;
    }


}

.proyecto {
    padding: 1.5rem;
    border: 1px solid v.$gris;
    box-shadow: 0px 2px 15px 2px v.$gris;
    border-radius: .5rem;

    &__nombre {
        margin: 0;
        text-align: center;
        font-size: 2rem;
        font-weight: v.$bold;
    }

    &__descripcion {
        text-align: left;
        margin: 1rem 0;
    }

    &__enlace {
        @include m.boton(v.$azul, v.$blanco);
    }
}

.tecnologia {
    &__icono {
        width: 4rem;
    }
}