@use 'base/variables' as v;
@use 'base/mixins' as m;

.admin-proyecto {
    padding: 2rem;
    @include m.grid(1, 1rem);
    align-items: center;

    @include m.tablet {
        @include m.grid(2, 5rem);
    }

    &__nombre {
        text-align: center;
        font-size: 3rem;
        margin: 2rem 0;
    }

    &__descripcion {
        font-weight: v.$delgada;
    }

    &__detalle {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__url,
    &__fecha {
        font-size: 1.5rem;
        color: v.$gris-oscuro;
        margin: 0.5rem 0;
    }

    &__boton {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;

        &--activar {
            @include m.boton(v.$verde, v.$blanco);
        }

        &--desactivar {
            @include m.boton(v.$rojo, v.$blanco);
        }
    }


    &__acciones {
        display: flex;
        flex-direction: column;
        gap: 0;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            gap: 1rem;
        }

        

    }
}