@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.8rem;
    background-color: #F8FAFC;
}
a {
    text-decoration: none;
}

img {
    width: 100%;
    height: auto;
}
