body {
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  background-color: #F8FAFC;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  text-align: center;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  text-align: center;
  text-transform: uppercase;
  margin: 6rem 0 3rem 0;
  color: #4756df;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  color: #1E293B;
  margin-bottom: 5rem;
  color: #00c8c2;
}

[class$=__formulario] {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.image {
  position: relative;
  width: 400px;
}
.image__img {
  display: block;
  width: 100%;
}
.image__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  color: #ffffff;
  font-family: "Quicksand", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.25s;
}
.image__overlay--blur {
  backdrop-filter: blur(5px);
}
.image__overlay--primary {
  background: #009578;
}
.image__overlay > * {
  transform: translateY(20px);
  transition: transform 0.25s;
}
.image__overlay:hover {
  opacity: 1;
}
.image__overlay:hover > * {
  transform: translateY(0);
}
.image__title {
  font-size: 2em;
  font-weight: bold;
}
.image__description {
  font-size: 1.25em;
  margin-top: 0.25em;
}

.proyectos {
  margin: 6rem 0;
}
.proyectos__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .proyectos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .proyectos__grid {
    grid-template-columns: unset;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.proyectos__enlace-contenedor {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.proyectos__enlace {
  background-color: #4756df;
  padding: 1.5rem 4rem;
  margin: 1rem 0 2rem 0;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
}
.proyectos__enlace:hover {
  background-color: #2435cf;
  cursor: pointer;
}
.proyectos__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}

.proyecto {
  padding: 1.5rem;
  border: 1px solid #e1e1e1;
  box-shadow: 0px 2px 15px 2px #e1e1e1;
  border-radius: 0.5rem;
}
.proyecto__nombre {
  margin: 0;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}
.proyecto__descripcion {
  text-align: left;
  margin: 1rem 0;
}
.proyecto__enlace {
  background-color: #4756df;
  padding: 1.5rem 4rem;
  margin: 1rem 0 2rem 0;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.proyecto__enlace:hover {
  background-color: #2435cf;
  cursor: pointer;
}

.tecnologia__icono {
  width: 4rem;
}

.mapa {
  height: 45rem;
  margin-top: 5rem;
}
.mapa__heading {
  margin: 0;
  text-align: left;
  font-size: 2rem;
  margin: 0.5rem 0;
  text-align: center;
}
.mapa__texto {
  font-size: 1.8rem;
  margin: 0;
  text-align: center;
  margin: 0;
}

.acerca__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.acerca__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .acerca__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.acerca__contenido-texto {
  padding: 5rem;
  line-height: 2;
}

.habilidades__contenedor {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.habilidad {
  width: min(95%, 60rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .habilidad {
    width: min(95%, 80rem);
    margin: 0 auto;
  }
}
.habilidad__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .habilidad__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .habilidad__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.habilidad__icono {
  padding: 2rem;
  border: 1px solid #e1e1e1;
  background-color: #FFFFFF;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.auth {
  width: min(95%, 80rem);
  margin: 0 auto;
  margin-bottom: 6rem;
}
.auth__formulario {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.contacto {
  width: min(95%, 80rem);
  margin: 0 auto;
  margin-bottom: 6rem;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__select {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.formulario__texto {
  margin-top: 3rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__submit {
  background-color: #4756df;
  padding: 1.5rem 4rem;
  margin: 1rem 0 2rem 0;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.formulario__submit:hover {
  background-color: #2435cf;
  cursor: pointer;
}

.acciones {
  display: flex;
  justify-content: space-between;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.4rem;
  font-weight: 300;
}

.barra {
  background-color: #4756df;
  padding: 1.5rem 0;
}
.barra__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .barra__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
  text-transform: uppercase;
}
@media (min-width: 1024px) {
  .barra__logo {
    text-align: left;
    margin: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2rem;
  padding: 2rem;
  text-align: center;
  font-weight: 700;
}

.header {
  height: 100vh;
  background-image: linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/hero.avif) 1x, url(../img/hero.webp) 1x, url(../img/hero.jpg) 1x);
  background-repeat: no-repeat;
  background-image: cover;
  background-size: cover;
  display: flex;
  align-items: center;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #4756df, #00c8c2);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  text-transform: unset;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
    display: inline-block;
  }
}
.header__texto {
  font-size: 3rem;
  font-weight: 700;
  color: #FFFFFF;
}

.footer {
  background-color: #4756df;
}
.footer__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
  padding: 4rem 0;
}
.footer__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__heading {
  color: #FFFFFF;
  font-weight: 700;
  text-align: left;
}
.footer__texto {
  color: #00c8c2;
  margin: 1rem 0;
}
.footer__copyright {
  background-color: #2536d3;
  padding: 0.2rem 0;
}
.footer__copyright-texto {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.4rem;
}

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: 700;
}
.alerta__error {
  background-color: #ffcbcb;
  border-left: 0.5rem solid #cb0000;
  color: #cb0000;
}
.alerta__exito {
  background-color: #bdff9f;
  border-left: 0.5rem solid #329f00;
  color: #329f00;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #1E293B;
  padding: 2rem;
}
.dashboard__header-grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 1024px) {
  .dashboard__logo {
    text-align: left;
    margin: 0;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}
.dashboard__submit--logout:hover {
  color: #4756df;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
  width: 12rem;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard__enlace {
  color: #6f8ab6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #40587f;
  color: #FFFFFF;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
}
.dashboard__contenido {
  padding: 1rem 1rem 3rem 1rem;
  width: 100%;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
  }
}
.dashboard__boton {
  background-color: #4756df;
  padding: 1.5rem 4rem;
  margin: 1rem 0 2rem 0;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.4rem;
}
.dashboard__boton:hover {
  background-color: #2435cf;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 4rem;
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
  width: 20%;
}
.table__tr {
  display: flex;
}
.table__tr:nth-child(even) {
  background-color: #F8FAFC;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
  width: 20%;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
.table__td-detalle, .table__th-detalle {
  width: 60%;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
  cursor: pointer;
}
.table__accion--editar {
  color: #4756df;
}
.table__accion--eliminar {
  color: #cb0000;
}

.admin-proyecto {
  padding: 2rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
@media (min-width: 768px) {
  .admin-proyecto {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.admin-proyecto__nombre {
  text-align: center;
  font-size: 3rem;
  margin: 2rem 0;
}
.admin-proyecto__descripcion {
  font-weight: 300;
}
.admin-proyecto__detalle {
  display: flex;
  flex-direction: column;
